<template>
	<v-row style="justify-content: center;">
		<v-col cols="9">
			<v-card color="transparent" elevation="24">
				<v-card-text class="pa-10">
					<v-card elevation="24">
						<v-row style="justify-content: center;">
							<v-col cols="8">
								<v-row>
									<v-col cols="6">
										<v-text-field :label="contact_dict.name.title" v-model="contact_dict.name.model"></v-text-field> 
									</v-col>
									<v-col cols="6">
										<v-text-field :label="contact_dict.first_name.title" v-model="contact_dict.first_name.model"></v-text-field>
									</v-col>
								</v-row>
							</v-col>
							<v-col cols="8">
								<v-text-field :label="contact_dict.mail.title" v-model="contact_dict.mail.model"></v-text-field>
							</v-col>

							<v-col cols="8">
								<v-row no-gutters>
									<v-col cols="12">
										<v-select
										:items="options"
										filled
										label="Motif de votre demande"
										v-model="option_selected"
										></v-select>
									</v-col>
									<v-col cols="5" v-if="option_selected === 'Autre'">
										<v-text-field label="Précisez ..."></v-text-field>
									</v-col>
								</v-row>
							</v-col>

							<v-col cols="8">
								<v-text-field :label="contact_dict.msg.title" v-model="contact_dict.msg.model"></v-text-field>
							</v-col>

							<v-col cols="12" style="text-align: center;">
								<v-btn @click="send_msg()" :disabled="btn_disabled"> Envoyer </v-btn><v-btn> Annuler </v-btn>
							</v-col>
						</v-row>
						
					</v-card>
				</v-card-text>
			</v-card>
		</v-col>
		<v-snackbar v-model="snackbar" dark color="success" timeout="10000" max-width="400">
			<v-row no-gutters>
				<v-col cols="10">
					Nous avons bien reçu votre demande et vous aurez très prochainement des nouvelles de nous !
				</v-col>
				<v-col cols="2">
					<v-btn color="white" text @click="snackbar = !snackbar"> Fermer </v-btn>
				</v-col>
			</v-row>
		</v-snackbar>
	</v-row>
</template>

<script>
export default {
	name: "ContactMe",
	data() {
		return {
			contact_dict: {
				name: {title: "Nom", model: ''},
				first_name: {title: "Prénom", model: ''},
				mail: {title: "E-mail", model: ''},
				msg: {title: "Votre Message", model: ''},
			},
			options: ["Un marriage", "Un anniversaire", "Un baptême", "Autre"],
			option_selected: '',
			snackbar: false,
			date: 0,
			date_at_click: 0,
			btn_disabled: false,
		}
	},
	mounted: function() {
		this.time()
		this.check()
	},
	methods: {
		send_msg() {
			this.snackbar = true;
			this.date_at_click = new Date().getTime() + 10000;
			this.btn_disabled = true;
		},
		time() {
			this.date = new Date().getTime();
			setInterval(this.time, 1000);
		},
		check() {
			if (this.date > this.date_at_click) {
				this.btn_disabled = false;
			}
			setInterval(this.check, 1000);
		}
	},
}
</script>